 revComp(dna) {
  let revComp = [];
  for(let i=0;i<dna.length;i++){
    if(dna[i]==='A'){revComp.push('T')} 
    else if(dna[i]==='C') {revComp.push('G')} 
    else if(dna[i]==='G') {revComp.push('C')}
    else if(dna[i]==='T') {revComp.push('A')}
    else if(dna[i]==='a') {revComp.push('t')} 
    else if(dna[i]==='c') {revComp.push('g')}
    else if(dna[i]==='g') {revComp.push('c')}
    else if(dna[i]==='t') {revComp.push('a')}
  }
  return revComp.reverse().join('');
}
