  downloadApeFile() {
    fetch(window.location.origin+'/fly_templates/empty_ape.txt').then((res)=>{return res.text();}).then((res)=>{
      const data = res;
      fetch(window.location.origin+'/fly_templates/feature.txt').then((res)=>{return res.text()}).then((res2)=>{
        const feature = res2;
        const newFeature = (loc, name, color) => {
          return feature
          .split('*featureLoc*').join(loc)
          .split('*featureName*').join(name)
          .split('*featureColor*').join(color);
        };
        let gene = this.state.sequence;
        const target = this.state.targets[0].distal.toString()+this.state.targets[0].proximal.toString();
        const targetMatch = gene.toLowerCase().match(target.toLowerCase());
        const revTargetMatch = gene.toLowerCase().match(this.revComp(target.toLowerCase()));
        let targetI;
        if (targetMatch) {
          targetI = targetMatch.index+1;
        } else if (revTargetMatch) {
          targetI = revTargetMatch.index;
        }
        const pamStart = revTargetMatch?targetI-2:targetI+20;
        const start = parseInt(this.state.highlights.start.location);
        const stop = parseInt(this.state.highlights.stop.location); 
        gene = !this.state.currentPam?gene:gene.substr(0,pamStart-1)+this.state.currentPam+gene.substr(pamStart+2,gene.length);
        const featureArr = [
          newFeature(start+'..'+(start+2),'Start Codon','#df2935'),
          newFeature(stop+'..'+(stop+2),'Stop Codon','#df2935'),
          newFeature((-9+parseInt(this.state.highlights['hom5']['location']))+'..'+(-9+parseInt(this.state.highlights['hom5']['location'])+this.state.highlights['hom5']['length']),"5' Homology Arm",'#fdca40'),
          newFeature((-9+parseInt(this.state.highlights['hom3']['location']))+'..'+(-9+parseInt(this.state.highlights['hom3']['location'])+this.state.highlights['hom3']['length']),"3' Homology Arm",'#fdca40'),
          newFeature((-9+parseInt(this.state.highlights['seq5']['location']))+'..'+(-9+parseInt(this.state.highlights['seq5']['location'])+this.state.highlights['seq5']['length']),"5' Sequence Primer",'#fdca40'),
          newFeature((-9+parseInt(this.state.highlights['seq3']['location']))+'..'+(-9+parseInt(this.state.highlights['seq3']['location'])+this.state.highlights['seq3']['length']),"3' Sequence Primer",'#fdca40'),
          newFeature(targetI+'..'+(parseInt(targetI)+20),"Target",'#136F5E'),
          newFeature(pamStart+'..'+(parseInt(pamStart)+2),"Pam",'#42ED5E'),
        ];
        const makeGeneArr = () => {

          let geneArr = [];
          const spaces = (str) => {
            let spaceArr = [];
            for(let i=0;i<9-str.length;i++){
              spaceArr.push('');
            }
            return spaceArr;
          }
          for(let i=0;i<gene.length;){
            if(i%50===0){
              geneArr.push('\n');                  
            }
            if(i===0||i%50===0) {
              const currentNum = (i+1).toString();
              geneArr.push(spaces(i+1).join(' ')+(i+1)+' ');
            }
            if(i+10>gene.length){
              geneArr.push(gene.slice(i,gene.length));
            } else {
              geneArr.push(gene.slice(i,i+10));
            }
            geneArr.push('');
            i=i+10;
          }
          return geneArr.join(' ');
        }
        const months = ["JAN","FEB","MAR","APR","MAY","JUN","JUL","AUG","SEP","OCT","NOV","DEC"]
        const date = new Date();
        const day = date.getDate();
        const month = months[date.getMonth()];
        const year = date.getFullYear();
        const newData = data
        .split('*FEATURES*').join(featureArr.join(''))
        .split('*name*').join(this.state.geneName)
        .split('*length*').join(this.state.sequence.length)
        .split('*date*').join(day+'-'+month+'-'+year)
        .split('*GENE*').join(makeGeneArr())
        ;
        const design = newData;
        var filename = this.state.geneName+".ape";
        var blob = new Blob([design], {
         type: "text/plain;charset=utf-8"
        });
        saveAs(blob, filename);     
      });
    });
  }

